	module.exports = {"data":{"site":{"siteMetadata":{"title":"RORSCHARG","author":"Rorscharg"}},"markdownRemark":{"id":"/home/calm/Programming/Repos/rorscharg.github.io/src/pages/blog/overstimulated-brain-makes-work-boring/index.md absPath of file >>> MarkdownRemark","html":"<p>I’ve done work on side projects before (didn’t manage to release something major yet). I like programming, but when I start spending too much time doing it, it loses its meaning, charm and magic and  becomes a chore.</p>\n<p>When I was working on side projects, I wasn’t excited to go to work and solve problems anymore, I’d still be pretty productive anyway but it wasn’t as fun. When you spent two hours working the evening before, being at work working again is not as nice as when you’re completely rested. </p>\n<p>I think side projects shouldn’t be in the same field, or different enough from your day job so your brain doesn’t think you’re doing the same thing. Reducing the amount of time spent on the side project each week might also work.</p>","frontmatter":{"title":"Overstimulated Brain Makes Work Boring","date":"September 09, 2018","image":{"childImageSharp":{"responsiveSizes":{"src":"/static/sipping-84ccf3a941375dd507667ce356d085b1-4975f.jpg","srcSet":"/static/sipping-84ccf3a941375dd507667ce356d085b1-40311.jpg 500w,\n/static/sipping-84ccf3a941375dd507667ce356d085b1-40646.jpg 1000w,\n/static/sipping-84ccf3a941375dd507667ce356d085b1-4975f.jpg 1920w","sizes":"(max-width: 1920px) 100vw, 1920px"}}}}}},"pathContext":{"slug":"/blog/overstimulated-brain-makes-work-boring/","previous":{"fields":{"slug":"/blog/stubborn/"},"frontmatter":{"title":"Stubborn","image":{"id":"/home/calm/Programming/Repos/rorscharg.github.io/src/pages/blog/stubborn/youquit.jpg absPath of file"}}},"next":null}}

